@media (max-width: 576px) {
	.dragdrop-zone {
		min-height: 70px;
		padding: 0 20px;
		span {
			line-height: 1.2;
			font-size: 14px; } }
	.file-upload-img {
		width: 40px;
		height: 40px; } }
