.btn-container-sales-catalog {
  text-align: right;
  padding-top: 23px; }
.sales-search {
  margin: 30px 0; }
.sales-item {
  background-color: $whiteColor;
  position: relative;
  margin-bottom: 30px;
  a {
    text-decoration: none; }

  h2 {
    font-size: $mediumSize;
    text-transform: initial;
    // margin: 0 0 15px
    a {
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      line-clamp: 2;
      position: relative;
      overflow: hidden;
      &:hover {
        color: $blueColor; } } } }
.sales-item-price {
  font-weight: 700;
  color: $blueColor; }
.sales-item-type {
  font-size: $smallSize;
  color: $whiteColor;
  padding: 2px 20px 1px;
  border-radius: 15px;
  background-color: $grayColor;
  @include positionElement(auto, auto, -0px, -4px, right, top, 1);

  &.sale {
    background-color: #A55EFF; }
  &.anons {
    background-color: #EC76CB; }
  &.knit {
    background-color: #FEAB4A; }
  &.gift {
    background-color: #ABE155; } }
.sales-date-item {
  font-size: $smallSize;
  margin-bottom: 4px; }
.sales-item-info {
  margin-top: 8px;
  margin-bottom: 8px; }
.sales-item-hidden {
  display: none; }
