@media (max-width: 576px) {
  .sales-item {
    margin-bottom: 8px;

    h2 {
      font-size: 14px; }
    .btn--blue {
      // background-color: $whiteColor
      // color: $blueColor
      // border-color: $blueColor
      padding: 3px 0 2px;
      font-size: 12px; }
    .sales-price {
      font-size: 12px; } }
  .sales-item-type {
    top: 15px;
    font-size: 12px;
    padding: 2px 6px 1px; }
  .sales-item-img {
    height: 115px;
    border-radius: 12px; }
  .sales-search {
    margin: 4px 0 30px;
    label {
      font-size: 12px; } }
  .sales-date-item {
    font-size: 12px; }
  .sales-date-item {
    margin-bottom: 5px; }
  .companies-item-stub,
  .sales-item-stub {
    height: 115px;
    border-radius: 13px;
    margin-top: 23px; } }
