.search-container {
  position: relative;
  z-index: 3;
  .spinner {
    left: auto;
    right: 8px; }
  .preloader-container {
    @include positionElement(30px, 100%,0,0);
    opacity: 0;
    &.active {
      opacity: 1; } } }
// .search-ajax
