.emodsy-container {
  position: relative;
  margin: auto; }

.emodsy-btn {
  width: 30px;
  height: 30px;
  cursor: pointer;
  @include bgImageEl("../images/Zoonika_icon-03.svg");
  cursor: pointer;
  &:hover {
    background-image: url("../images/Zoonika_icon-04.svg"); } }

.emodsy-popup {
  @include positionElement(250px, 230px, -110px, 50px, left, bottom);
  background-color: $whiteColor;
  border-radius: 15px;
  box-shadow: $shadow;
  padding: 8px;
  font-size: 0;
  overflow-y: auto; }
.emodsy-el {
  display: inline-block;
  width: 60px;
  height: 60px;
  border: 1px solid $grayColorSecond;
  border-radius: 15px;
  cursor: pointer;
  margin: 5px;
  &:hover {
    border-color: $blueColor; } }
